<template>
    <div class="box">
        <div class="box-header with-border">
            <h3 class="box-title">Thêm banner mới</h3>
        </div>
        <div class="box-body">
            <form @submit.prevent="handleSubmit">
                <div class="box-body">
                    <div class="row">
                        <div class="col-lg-12">
                            <div class="form-group">
                                <label>Name</label>
                                <input v-model="post.name" name="name" v-validate="'required|min:3|max:255'" type="text" :class="{'form-control':true, 'error-boder': errors.first('name')}" placeholder="Nhập tên danh mục...">
                                <span class="error">{{ errors.first("name") }}</span>
                            </div>
                            <div class="form-group">
                                <label>Link</label>
                                <input v-model="post.link" name="link" v-validate="'max:255'" type="text" :class="{'form-control':true, 'error-boder': errors.first('name')}" placeholder="Nhập tên danh mục...">
                                <span class="error">{{ errors.first("link") }}</span>
                            </div>
                            <div class="form-group">
                                <label>Title</label>
                                <input v-model="post.title" name="title" v-validate="'max:255'" type="text" :class="{'form-control':true, 'error-boder': errors.first('name')}" placeholder="Nhập tên danh mục...">
                                <span class="error">{{ errors.first("title") }}</span>
                            </div>
                            <div class="form-group">
                                <label>Description</label>
                                <input v-model="post.description" name="description" v-validate="'max:255'" type="text" :class="{'form-control':true, 'error-boder': errors.first('name')}" placeholder="Nhập tên danh mục...">
                                <span class="error">{{ errors.first("description") }}</span>
                            </div>
                            <div class="form-group">
                                <label>Detail</label>
                                <vue-editor v-model="post.detail" name="content" v-validate="'required|min:5'" />
                                <span class="error">{{ errors.first("detail") }}</span>
                            </div>
                           
                        </div>
                    </div>
                    <button type="submit" class="btn btn-primary" >Thêm</button>
                </div>
                <!-- /.box-body -->
            </form>
        </div>
        <!-- /.box-body -->
    </div>
</template>

<script>
import axios from "axios";
import { Validator } from 'vee-validate';
import { VueEditor } from "vue2-editor";

export default {
    metaInfo: {
        title: "Thêm banner"
    },
    components:{VueEditor},
    data(){
        return {
            api: this.$store.state.domainApi,
            post:{
                name: "",
                url: "",
                image:""
            },
            file:{},
            
            dictionary:{
                vi: {
                    custom: {
                        // Tủy chỉnh theo từng trường
                        'name' : {
                            required: 'Vui lòng nhập tên banner !',
                            min: 'Tên danh mục tối thiểu 3 ký tự !',
                            max: 'Tên danh mục không được vượt quá 255 ký tự !'
                        },
                        "image":{
                            required: "Vui lòng chọn hình banner !",
                            image: "Vui lòng chọn định dạng hình ảnh !"
                        },
                        "url":{
                            max: 'Title không được vượt quá 255 ký tự !'
                        }
                    }
                }
            }
        }
    },
    methods: {
        handleSubmit() {
            this.$validator.validate().then(valid => {
                if (valid) {
                    const formData = new FormData();
                    
                    formData.set("name", this.post.name);
                    formData.append("url", this.post.url);
                    formData.append("image", this.file);

                    axios.post(`${this.api}/admin/banner`, formData, {
                        headers:{
                            Authorization: `Bearer ${localStorage.token}`
                        }
                    })
                    .then(response => {
                        if(response.data.success===1){
                            this.flash(response.data.message, 'success', {
                                timeout: 3000
                            });
                            this.$router.push({name: "route.admin.banner.index"});
                        }else{
                            this.flash(response.data.message, 'error', {
                                timeout: 3000
                            });
                        }
                        
                    });
                    
                }
            });
        },
        getData(){
            axios.get(`${this.api}/admin/about-footer/${this.$route.params.id}`, {
                headers:{
                    Authorization: `Bearer ${localStorage.token}`
                }
            })
            .then(response => {
                this.post = response.data;
            });
        }
    },
    created(){
        Validator.localize(this.dictionary);
        const validator = new Validator({ first_name: 'alpha' });
        validator.localize('vi');
        this.getData();
    }
}
</script>
